<template>
  <!-- 视频 -->
  <div class="video">
    <div
      class="item-box"
      v-for="(item, index) in videoData"
      :key="index"
      @click="getDetail(item.r.id)"
    >
      <div class="userinfo" @click.stop="goUserDetail(item.r.a.id)">
        <div class="headimg">
          <img class="auto-img" :src="item.r.a.p" alt="" />
        </div>
        <div class="name">{{ item.r.a.n }}</div>
        <div class="lv">lv{{ item.r.a.lvl }}</div>
      </div>
      <div class="video-box">
        <video class="auto-img" :src="item.r.vfurl"></video>
        <div class="img-box">
          <img class="auto-img" src="../assets/icon/play.png" alt="" />
        </div>
      </div>
      <div class="collect">
        <div class="left" v-show="item.r.collect_users.length != 0">
          <div
            class="user-p"
            v-for="(userItem, userIndex) in item.r.collect_users"
            :key="userIndex"
          >
            <div class="item">
              <img class="auto-img" :src="userItem.p" alt="" />
            </div>
          </div>
        </div>
        <div class="collect-count">{{ item.r.collect_count_text }}</div>
        <div class="right" @click.stop="goLogin(index)">
          <img
            class="auto-img collect-img"
            src="../assets/guanzhu/shoucang.png"
            alt=""
          />
        </div>
      </div>
      <div class="title">{{ item.r.n }}</div>
    </div>

    <van-list
      offset="100"
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <br />
    </van-list>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  name: 'Video',
  data() {
    return {
      videoData: [],
      collectData: [],
      offset: 0,
      limit: 10,
      loading: false,
      finished: false,
      isCollect: false,
    }
  },
  created() {
    this.getVideoData()
  },
  methods: {
    onLoad() {
      this.offset = this.offset + 10
      setTimeout(() => {
        this.axios({
          method: 'get',
          url:
            'https://apis.netstart.cn/douguo/home/recommended/' +
            this.offset +
            '/' +
            this.limit,
        })
          .then((result) => {
            this.images = result.data.result.banner
            result.data.result.list.forEach((e) => {
              if (e.type === 1) {
                this.videoData.push(e)
              }
            })
          })
          .catch((err) => {})
        this.loading = false
      }, 1000)
    },

    getVideoData() {
      this.axios({
        method: 'get',
        url:
          'https://apis.netstart.cn/douguo/home/videos/' +
          this.offset +
          '/' +
          this.limit,
      })
        .then((result) => {
          if (result.status === 200) {
            result.data.result.list.forEach((e) => {
              if (e.type === 1) {
                this.videoData.push(e)
              }
            })
          }
        })
        .catch((err) => {})
    },

    getDetail(id) {
      this.$router.push({ name: 'Detail', params: { id } })
    },

    goLogin(index) {
      let collectImg = document.querySelectorAll('.collect-img')

      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isCollect) {
          collectImg[index].src = require('../assets/icon/liangwujiaoxing.png')
          Toast('收藏成功')
          this.isCollect = true
        } else {
          collectImg[index].src = require('../assets/icon/collect-active.png')
          Toast('取消收藏')
          this.isCollect = false
        }
      }
    },

    goUserDetail(id) {
      this.$router.push({ name: 'Userdetail', params: { id } })
    },
  },
}
</script>

<style lang="less" scoped>
.video {
  padding-top: 100px;
  padding-bottom: 50px;
  .item-box {
    margin-bottom: 20px;
    .userinfo {
      display: flex;
      padding: 10px;
      align-items: center;
      .headimg {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        overflow: hidden;
        background-color: red;
      }
      .name {
        font-weight: bold;
        margin: 0 5px;
      }
      .lv {
        color: #facc30;
        font-style: italic;
      }
    }
    .video-box {
      position: relative;
      width: 100%;
      .img-box {
        position: absolute;
        bottom: 10px;
        right: 10px;
        width: 30px;
        height: 30px;
      }
    }
    .collect {
      display: flex;
      padding: 10px;
      align-items: center;
      .left {
        display: flex;
        align-items: center;
		position: relative;
        .user-p {
          display: flex;
          .item {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            overflow: hidden;
            background-color: #facc30;
          }
        }
        .user-p:nth-child(1) {
          position: absolute;
          left: 0;
          z-index: 2;
        }
        .user-p:nth-child(2) {
          position: absolute;
          left: 13px;
          z-index: 3;
        }
        .user-p:nth-child(3) {
          position: absolute;
          left: 26px;
          z-index: 4;
        }
        .user-p:nth-child(4) {
          position: absolute;
          left: 39px;
          z-index: 5;
        }
      }
      .collect-count {
        margin-left: 65px;
        font-size: 14px;
      }
      .right {
        margin-left: auto;
        width: 25px;
        height: 25px;
      }
    }
    .title {
      padding: 0 10px;
	  font-size: 14px;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
</style>
